import { CdkTable } from '@angular/cdk/table';
import { AfterViewInit, Component, HostBinding, HostListener, QueryList } from '@angular/core';
import { ColumnDefDirective } from '../../directives/column-def.directive';

@Component({
  selector: 'table[dg-table]',
  templateUrl: './table.component.html',
  styleUrls: ['./table.component.scss'],

  providers: [
    { provide: CdkTable, useExisting: TableComponent }
  ]
})
export class TableComponent<T> extends CdkTable<T> implements AfterViewInit {
  @HostBinding('style.gridTemplateColumns')
  public get gridTemplateColumns(): string {
    let columns = Array.from(this._contentHeaderRowDefs.first.columns);

    return (this._contentColumnDefs as QueryList<ColumnDefDirective>)
      .filter(({ name: column }) => columns.includes(column))
      .map(({ size }) => size)
      .join(' ');
  }

  @HostListener('window:resize')
  public updateStickyStyles(): void {
    this.updateStickyHeaderRowStyles();
    this.updateStickyFooterRowStyles();
    this.updateStickyColumnStyles();
  }

  public ngAfterViewInit(): void {
    this.updateStickyStyles();
  }
}
