import { CdkRowDef as CdkContentRowDef } from '@angular/cdk/table';
import { Directive, Input } from '@angular/core';

@Directive({
  selector: '[dgContentRowDef]',

  providers: [
    { provide: CdkContentRowDef, useExisting: ContentRowDefDirective }
  ]
})
export class ContentRowDefDirective<T> extends CdkContentRowDef<T> {
  @Input('dgContentRowDefColumns')
  public columns!: Iterable<string>;

  @Input('dgContentRowDefWhen')
  public when: (index: number, rowData: T) => boolean = () => true;
}
